import Vue from 'vue'
import Router from 'vue-router'
//
import login from '@/page/login'
//
import main from '@/page/main'
import pageList from './page'
Vue.use(Router)

export default new Router({
    mode:'hash',
    //history
    // mode: 'history',
    routes: [{
            path: '/',
            name: 'main',
            //
            redirect: '/Index6',
            component: main,
            children: pageList
    },{
        path: '/login',
        name:'login',
        component: login,
    }]
})



