<template>
	<div class="Index22">
		<zyh-drop-down ref="child" :zyhType="type">
			<span></span>
			<div slot="dropdown_zyh">
				<li @click="active"></li>
				<li @click="active"></li>
				<li @click="active"></li>
				<li @click="active"></li>
			</div>
		</zyh-drop-down>
	</div>
</template>

<script>
	export default {
		name: 'Index22',
		data() {
			return {
				//hover click  hover
				type:"hover"
			}
		},
		methods: {
			active(){
				this.$refs.child.hide();
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



