<template>
	<div class="zyhZtree">
		<li>
		    <div
		      :class="{bold: isFolder}"
		      @click="toggle">
		      <input v-if="toolbar.ischeck" type="checkbox" v-model="model.checked" @click="noBubble" />
		      <input v-show="edits" v-model="model.name" @blur="noedits" @click="noBubble" />
		      <span v-show="!edits">{{model.name}}</span>
		      <span v-if="isFolder">[{{open ? '-' : '+'}}]</span>
		    </div>
		    <li v-if="toolbar.isadd" class="add" @click="addChild"></li>
		    <li v-if="toolbar.isdelete" class="remove" @click="remove"></li>
		    <li v-if="toolbar.edit" class="edit" @click="edit"></li>
		    <div class="clearBoth"></div>
		    <ul v-show="open" v-if="isFolder">
		      <zyh-ztree v-if="model.name"
		        class="item"
		        v-for="model in model.children"
		        :key="model.id"
		        :model="model"
		        :toolbar="toolbar"
		        @parentMethod="macSelect"
		         >
		      </zyh-ztree>
		    </ul>
		</li>
	</div>
</template>
<script>
	/*******************************/
	import Vue from 'vue'
	export default {
		props: [
			'model','toolbar'
		],
		name:'zyhZtree',
		data() {
			return {
				//
				edits:false,
				//
				data:null,
				open: false,
				idStr:0
			}
		},
		computed: {
			isFolder() {
				return this.model.children && this.model.children.length;
			}
		},
		methods: {
			//
			macSelect(param){
				this.model.children.remove(param.childmodel); 
			},
			//
			toggle() {
				if(this.isFolder) {
					this.open = !this.open
				}
			},
			//
			addChild() {
				if(this.model.children){}else{
					Vue.set(this.model, 'children', [])
					this.open = true
				}
				this.model.children.push({
					name: '',
					checked:false,
					id:this.idStr++
				})
				
			},
			//
			edit(){
				this.edits = true;
				
			},
			noedits(){
				this.edits = false;
			},
			noBubble(event){
				event.cancelBubble = true;
			},
			//
			remove(){
//				if(this.model.children){
//					if(this.model.children.length){
//						alert('');
//						return;
//					}
//				}
				this.$emit('parentMethod',{'childmodel':this.model});
			}
		}
	}
</script>

<style scoped>
	.zyhZtree{
		padding-left: 20px;
	}
	.item {
	  cursor: pointer;
	}
	.bold {
	  font-weight: bold;
	}
	ul {
	  padding-left: 1em;
	  line-height: 1.5em;
	  list-style-type: dot;
	}
	.add,.remove,.edit{
		color: blue;
		cursor: pointer;
		margin-left: 20px;
		list-style: none;
	}
	.remove{
		color: red;
	}
	.edit{
		color: green;
	}
	li{
		float: left;
		width: auto;
	}
	.clearBoth{
		clear: both;
	}
</style>


