<script>
	export default {
		name: 'Index2',
		data() {
			return {
				datas:[
					{name:'',active:true},
					{name:'',
						child:[
							{name:'1'},
							{name:'2'},
							{name:'3'},
							{name:'4',
								child:[
									{name:'1'},
									{name:'2'},
									{name:'3',
										child:[
											{name:'1'},
											{name:'2'},
											{name:'3'},
											{name:'4'}
										]
									}
								]
							}
						]
					},
					{name:''},
					{name:''}
				]
			}
		},
		methods: {

		}
	}
</script>

